.outerWrapper {
  height: 100%;
  display: flex;
}
.wrapper {
  flex: 1 1 auto;
  display: flex;
  height: 100%;
  overflow: hidden;
  border-radius: 0 !important;
  border: none !important;
  margin: 0 !important;
  transform: none !important;
}
.wrapper.invisible {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  opacity: 0;
  visibility: hidden;
}
.sidebar {
  padding: 8px;
  background: var(--c-main-bg, #fff);
}
.sidebar .item {
  width: 100%;
  min-height: 31px;
  margin-left: 0 !important;
}
.sidebar .item.itemLink {
  color: var(--c-link, #538ec0);
}
.sidebar .item[class^='itemActive_'],
.sidebar .item[class*=' itemActive_'] {
  color: var(--c-primary, #4183c4) !important;
}
.sidebar .item[class^='itemActive_']:hover,
.sidebar .item[class*=' itemActive_']:hover {
  color: var(--c-primary-hover, #538ec0) !important;
}
.sidebar .item.itemActive_recent svg path:not([fill='transparent']),
.sidebar .item.itemActive_favorites svg path:not([fill='transparent']),
.sidebar .item.itemActive_directory svg path:not([fill='transparent']),
.sidebar .item.itemActive_dialpad svg path:not([fill='transparent']),
.sidebar .item.itemActive_calendar svg path:not([fill='transparent']),
.sidebar .item.itemActive_help svg path:not([fill='transparent']) {
  fill: var(--c-primary, #4183c4) !important;
}
.sidebar .item.itemActive_recent:hover svg path:not([fill='transparent']),
.sidebar .item.itemActive_favorites:hover svg path:not([fill='transparent']),
.sidebar .item.itemActive_directory:hover svg path:not([fill='transparent']),
.sidebar .item.itemActive_dialpad:hover svg path:not([fill='transparent']),
.sidebar .item.itemActive_calendar:hover svg path:not([fill='transparent']),
.sidebar .item.itemActive_help:hover svg path:not([fill='transparent']) {
  fill: var(--c-primary-hover, #538ec0) !important;
}
.sidebar .item.itemActive_calendar svg path,
.sidebar .item.itemActive_knowledge-base svg path {
  stroke: var(--c-primary, #4183c4) !important;
}
.sidebar .item.itemActive_calendar:hover svg path,
.sidebar .item.itemActive_knowledge-base:hover svg path {
  stroke: var(--c-primary-hover, #538ec0) !important;
}
.sidebar .icon {
  width: 24px;
}
.header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  background: var(--c-header-bg, #fafafa);
}
.header .pageTitle {
  margin: 0 !important;
  line-height: 48px !important;
  height: 48px !important;
  width: 100%;
  text-align: center;
  flex-grow: 1;
}
.headerTop {
  width: 100%;
  display: grid;
  grid-gap: 5px;
  grid-template-columns: minmax(0, 1fr) auto minmax(0, 1fr);
  grid-template-rows: 48px;
  padding: 0 16px;
}
.agentState {
  display: flex;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
}
.agentStateText {
  font-size: 11px;
  line-height: 14px;
}
.rightButtons {
  justify-self: flex-end;
  display: flex !important;
  justify-content: center;
  align-items: center;
  position: relative;
}
.rightButtons > *:not(:last-child) {
  margin-right: 8px;
}
.rightButtons .popout {
  box-shadow: none !important;
  border: 1px solid transparent !important;
  height: 32px;
  width: 32px;
  margin-right: 6px !important;
  padding: 0 0 4px 4px !important;
}
.rightButtons :global(.ui.button).directChat {
  width: 32px;
  height: 32px;
  padding: 0;
  box-shadow: none;
  border: 1px solid var(--c-main-border, rgba(34, 36, 38, 0.3));
  transition: all 0.3s ease;
}
.rightButtons :global(.ui.button).directChat svg path {
  transition: fill 0.3s ease;
}
.rightButtons :global(.ui.button).directChat:hover {
  border: 1px solid var(--c-main-border-hover, rgba(34, 36, 38, 0.45));
}
.rightButtons :global(.ui.button).directChat:hover svg path {
  fill: rgba(0, 0, 0, 0.8);
}
.rightButtons .hamburger {
  position: relative;
  box-shadow: none !important;
  border: 1px solid var(--c-main-border, rgba(34, 36, 38, 0.3)) !important;
  transition: all 0.3s ease;
  border-radius: 4px;
  height: 32px;
  width: 32px;
  font-size: 18px !important;
  padding: 0 !important;
  margin: 0 !important;
}
.rightButtons .hamburger:hover,
.rightButtons .hamburger:focus {
  border: 1px solid var(--c-main-border-hover, rgba(34, 36, 38, 0.45)) !important;
}
.pusher {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden !important;
}
.pusher.pusher {
  min-height: auto;
}
.pusher .content {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 0;
}
.offlineBar {
  margin-bottom: 8px;
}
.offlineErrorMessage {
  width: 100%;
  margin: 0 16px 8px !important;
}
.knowledgeBase {
  box-shadow: none !important;
  border: 1px solid transparent !important;
}
.knowledgeBase:hover {
  border: 1px solid var(--c-main-border-hover, rgba(34, 36, 38, 0.45)) !important;
}
.notifications {
  flex: 1 0 auto;
}
.mainMenuIconWrapper {
  width: 24px;
  margin-right: 30px !important;
  position: relative;
}
.errorFallback {
  position: absolute;
  top: 50%;
  left: 15px;
  width: calc(100% - 30px);
  transform: translateY(-50%);
  text-align: center;
}
:global(.ui.label).copiedLabel {
  position: absolute;
  left: -10px;
  transform: translateX(-100%);
  user-select: none;
  animation: fade-out 2s ease-out both;
  animation-delay: 1s;
}
:global(.ui.label).copiedLabel.inNotification {
  left: unset;
  transform: translate(4px, -2px);
}
@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.directChatLinkCopyNotification {
  cursor: pointer;
}
