.bar {
  position: relative;
  align-items: center;
  display: flex;
  flex-shrink: 0;
  font-size: 0.857rem;
  line-height: 24px;
  z-index: 5;
  width: 100%;
  border: none;
  height: 24px;
  padding: 0 1rem;
  color: var(--c-interaction-bar-text, #fff);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background: var(--c-interaction-bar-bg, #21ba45);
  cursor: pointer;
}
.icon {
  margin-right: 0.3rem;
  fill: white;
  display: flex;
}
.icon svg {
  width: 12px;
  height: 12px;
}
.desc {
  margin-right: 1rem;
  display: flex;
  align-items: center;
}
.unread {
  background: #fbbd08 0% 0% no-repeat border-box;
  color: var(--c-interaction-bar-text, #fff);
  right: 14px;
  position: absolute;
  width: 20px;
  height: 20px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-size: 12px;
}
