.overlayContainer {
  z-index: 3;
  position: absolute;
  top: 16px;
  left: 24px;
  right: 24px;
  display: flex;
  flex-flow: column nowrap;
}
.overlayContainer .notificationList > div + div {
  margin-top: 8px;
}
.notificationItem {
  width: 100%;
  border-radius: 4px;
  position: relative;
}
.notificationItem .notificationItemContent {
  height: 100%;
}
.notificationItem .background {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(86, 66, 109, 0.668);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  border-radius: 4px;
  backdrop-filter: blur(1px);
}
.notificationItem .content {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  color: var(--c-main-text-inverted, #fff);
  padding: 7px 16px 11px;
}
.notificationItem .content .displayRow {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 7px;
  flex-grow: 1;
}
.notificationItem .content .displayRow .chatIcon {
  height: 24px;
  width: 24px;
}
.notificationItem .content .displayRow .chatIcon img {
  height: 100%;
  width: 100%;
}
.notificationItem .content .displayRow .displayName {
  font-size: 1.286rem;
}
.notificationItem .content .displayRow .phoneNumber {
  font-size: 1.286rem;
  margin-left: 11px;
}
.notificationItem .content .controlsRow {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
}
.notificationItem .content .controlsRow .control {
  height: 32px;
  margin: 0;
  padding: 0 16px;
  font-size: 1rem;
  font-weight: 400;
}
.notificationItem .content .controlsRow .control + .control {
  margin-left: 8px;
}
.serviceRow {
  font-size: 18px;
  line-height: 1.1;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 7px;
}
.invertedButton {
  color: #fff !important;
  border: 1px solid #fff !important;
  background: transparent !important;
  box-shadow: none;
}
.invertedButton:focus,
.invertedButton:hover {
  background: transparent !important;
  opacity: 0.8;
}
